:root {
  --todo-bg: rgba(9, 13, 30, 0.95);
  --panel-bg: #050917;
  --panel-border: #1e253c;
  --panel-border-strong: #5ec7f4;
  --text-primary: #f4f6fb;
  --text-muted: #94a0bf;
  --accent: #5ec7f4;
  --danger: #ff6b6b;
  --card-radius: 16px;
}

.todoApp {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  color: var(--text-primary);
  padding: 2rem 1rem 3rem;
  max-width: 1200px;
  margin: 0 auto;
}

.todoAppHeader {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  align-items: flex-start;
  flex-wrap: wrap;
}

.todoAppHeader h2 {
  margin: 0.25rem 0;
}

.eyebrow {
  text-transform: uppercase;
  letter-spacing: 0.2em;
  font-size: 0.75rem;
  color: var(--text-muted);
  margin: 0;
}

.subtitle {
  margin: 0;
  color: var(--text-muted);
  max-width: 640px;
}

.statsPill {
  border: 1px solid var(--panel-border);
  border-radius: 999px;
  padding: 0.75rem 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-width: 140px;
}

.statsPill span {
  font-size: 1.85rem;
  font-weight: 700;
}

.statsPill small {
  color: var(--text-muted);
}

.sectionNavBar {
  background: rgba(5, 9, 23, 0.85);
  border: 1px solid var(--panel-border);
  border-radius: var(--card-radius);
  padding: 0.75rem;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.04);
}

.sectionNav {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 0.5rem;
}

.sectionNavButton {
  border-radius: var(--card-radius);
  border: 1px solid var(--panel-border);
  background: rgba(255, 255, 255, 0.02);
  color: var(--text-primary);
  padding: 0.85rem 1rem;
  text-align: left;
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
  font-size: 0.95rem;
  cursor: pointer;
  transition: border-color 0.2s ease, background 0.2s ease;
}

.sectionNavButton small {
  color: var(--text-muted);
  font-size: 0.75rem;
}

.sectionNavButton.is-active {
  border-color: var(--panel-border-strong);
  background: rgba(94, 199, 244, 0.12);
}

.sectionNavButton:disabled {
  opacity: 0.35;
  cursor: not-allowed;
}
.sectionNavButton:not(:disabled):hover,
.sectionNavButton:not(:disabled):focus-visible {
  border-color: var(--panel-border-strong);
  background: rgba(94, 199, 244, 0.18);
}

.sectionsGrid {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.sectionCard {
  background-color: var(--panel-bg);
  border: 1px solid var(--panel-border);
  border-radius: var(--card-radius);
  padding: 1.25rem;
  box-shadow: 0 5px 18px rgba(3, 7, 18, 0.45);
  display: none;
}

.sectionCard.is-active {
  display: block;
}

.ghostButton {
  background: transparent;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 999px;
  color: var(--text-primary);
  padding: 0.45rem 1.25rem;
  cursor: pointer;
  font-size: 0.85rem;
  transition: border-color 0.2s ease, color 0.2s ease;
}

.ghostButton:hover,
.ghostButton:focus-visible {
  border-color: var(--accent);
  color: var(--accent);
}

.cardHeader {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  align-items: flex-start;
  margin-bottom: 1rem;
}

.cardHeader h3 {
  margin: 0.25rem 0 0;
}

.cardHeader button {
  border: none;
  border-radius: 999px;
  padding: 0.65rem 1.5rem;
  background: var(--accent);
  color: #04111f;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s ease, transform 0.2s ease;
}
.cardHeader button:hover,
.cardHeader button:focus-visible {
  transform: translateY(-1px);
  background: #7fd6ff;
}
.cardHeader button:active {
  transform: translateY(0);
}

.newTaskForm,
.taskFilters,
.tasksList,
.editTaskForm,
.sectionCard--summary {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.02);
}

.newTaskForm {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 1rem;
  padding: 1rem;
}

.formField {
  display: flex;
  flex-direction: column;
  gap: 0.35rem;
}

.formField > label,
.formField > span {
  font-size: 0.85rem;
  font-weight: 600;
  color: var(--text-muted);
}

.newTaskForm input,
.newTaskForm textarea,
.newTaskForm select,
.taskFilters input,
.statusFilterChip,
.editTaskForm input,
.editTaskForm textarea,
.editTaskForm select,
.tasksList select {
  background: #000;
  border: 1px solid var(--panel-border);
  border-radius: 10px;
  color: var(--text-primary);
  padding: 0.65rem 0.85rem;
  font-size: 0.95rem;
}

.newTaskForm textarea,
.editTaskForm textarea {
  resize: vertical;
  min-height: 80px;
}

.newTaskForm button {
  align-self: flex-end;
  border: none;
  border-radius: 999px;
  padding: 0.85rem 1.75rem;
  font-weight: 600;
  cursor: pointer;
  background: #7c5dfa;
  color: #fff;
  height: fit-content;
  transition: background 0.2s ease, transform 0.2s ease;
}
.newTaskForm button:hover,
.newTaskForm button:focus-visible {
  background: #9c7dfc;
  transform: translateY(-1px);
}
.newTaskForm button:active {
  transform: translateY(0);
}

.taskFilters {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  padding: 1rem;
  margin-bottom: 1rem;
}

.filterRow {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
  align-items: center;
}

.filterRow input {
  flex: 1;
  min-width: 200px;
}

.statusChips {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.statusFilterChip {
  border-radius: 999px;
  border: 1px solid transparent;
  cursor: pointer;
  transition: border-color 0.2s ease, background 0.2s ease;
}

.statusFilterChip.is-active {
  border-color: var(--accent);
  background: rgba(94, 199, 244, 0.2);
  color: var(--accent);
}

.resetFilters {
  margin-left: auto;
  background: none;
  border: none;
  color: var(--text-muted);
  cursor: pointer;
  text-decoration: underline;
}

.tasksList {
  list-style: none;
  margin: 0;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  max-height: 520px;
  overflow-y: auto;
}

.tasksListEmpty {
  min-height: 160px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.25rem;
  color: var(--text-muted);
  text-align: center;
}

.taskCard {
  background: #050c1d;
  border: 1px solid transparent;
  border-radius: 14px;
  padding: 0.95rem;
  cursor: pointer;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

.taskCard:hover,
.taskCard:focus-within {
  border-color: rgba(94, 199, 244, 0.35);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.25);
}

.taskCard.is-selected {
  border-color: var(--accent);
  box-shadow: 0 0 0 2px rgba(94, 199, 244, 0.25);
}

.taskCardHeader {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  align-items: flex-start;
}

.taskCardHeader h4 {
  margin: 0.35rem 0 0;
}

.statusBadge {
  display: inline-flex;
  align-items: center;
  padding: 0.2rem 0.75rem;
  border-radius: 999px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin: 0;
}

.statusBadge--todo {
  background: rgba(94, 199, 244, 0.15);
  color: var(--accent);
}

.statusBadge--in_progress {
  background: rgba(255, 200, 87, 0.15);
  color: #f7a21b;
}

.statusBadge--blocked {
  background: rgba(255, 107, 107, 0.15);
  color: var(--danger);
}

.statusBadge--done {
  background: rgba(144, 238, 144, 0.15);
  color: #71f0b6;
}

.taskCardDescription {
  color: var(--text-muted);
  margin: 0.75rem 0;
  line-height: 1.5;
}

.taskCardFooter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.taskCardFooter label {
  display: flex;
  flex-direction: column;
  gap: 0.35rem;
  font-size: 0.85rem;
  color: var(--text-muted);
}

.deleteTaskButton {
  border: none;
  background: rgba(255, 255, 255, 0.07);
  color: var(--text-primary);
  width: 32px;
  height: 32px;
  border-radius: 50%;
  font-size: 1.25rem;
  line-height: 1;
  cursor: pointer;
}

.deleteTaskButton:hover,
.deleteTaskButton:focus-visible {
  background: rgba(255, 255, 255, 0.15);
}

.editTaskForm {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  padding: 1rem;
}

.editTaskForm button {
  align-self: flex-end;
  padding: 0.65rem 1.75rem;
  border-radius: 999px;
  border: none;
  cursor: pointer;
  background: var(--accent);
  color: #04111f;
  font-weight: 600;
  transition: background 0.2s ease, transform 0.2s ease;
}
.editTaskForm button:hover,
.editTaskForm button:focus-visible {
  background: #7fd6ff;
  transform: translateY(-1px);
}
.editTaskForm button:active {
  transform: translateY(0);
}

.sectionCard--summary {
  display: block;
}

.summaryText {
  white-space: pre-wrap;
  line-height: 1.5;
}

.errorText {
  color: var(--danger);
}

.errorText.compact {
  margin: 0;
}

.hint {
  font-size: 0.85rem;
  color: var(--text-muted);
}

@media (max-width: 640px) {
  .todoApp {
    padding: 1.5rem 0.5rem 2rem;
  }

  .sectionCard {
    padding: 1rem;
  }

  .cardHeader button,
  .newTaskForm button,
  .editTaskForm button {
    width: 100%;
  }

  .sectionNavButton {
    font-size: 0.9rem;
  }

  .taskFilters .filterRow {
    flex-direction: column;
    align-items: stretch;
  }

  .tasksList {
    max-height: none;
  }
}
